/* src/App.css */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&family=Playfair+Display:wght@400;700&display=swap');

:root {
  --primary-bg: #0d1a3a; /* Azul más profundo para un look premium */
  --secondary-bg: #1a2b5a;
  --card-bg: #2a2a4a;
  --text-primary: #ffffff;
  --text-secondary: #b0b8d8;
  --accent: #00aaff;
  --accent-dark: #0088cc;
  --error: #ff5555;
  --shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  background: linear-gradient(135deg, var(--primary-bg) 0%, var(--secondary-bg) 100%);
  color: var(--text-primary);
  min-height: 100vh;
  padding: 20px;
  text-align: center;
  position: relative;
  overflow-x: hidden;
}

.app-container {
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

/* Estilos para la pantalla de bienvenida */
.welcome-screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1;
  width: 100%;
  animation: fadeIn 1s ease-in-out;
}

.logo {
  width: 150px;
  height: auto;
  margin-bottom: 20px;
  animation: fadeIn 1s ease-in-out;
}

.tagline {
  font-family: 'Playfair Display', serif;
  font-size: 1.2rem;
  color: var(--text-secondary);
  margin-bottom: 30px;
  animation: slideUp 0.8s ease-in-out;
}

.info-link {
  font-size: 0.9rem;
  color: var(--text-secondary);
  text-decoration: none;
  margin-top: 20px;
  transition: color 0.3s ease;
  animation: fadeIn 1s ease-in-out;
}

.info-link:hover {
  color: var(--accent);
}

/* Estilos del botón */
.button {
  padding: 12px 25px;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  background: linear-gradient(135deg, var(--accent) 0%, var(--accent-dark) 100%);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow);
  width: 100%;
  max-width: 300px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
  animation: slideUp 0.8s ease-in-out;
}

.button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: 0.5s;
}

.button:hover::before {
  left: 100%;
}

.button:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(0, 170, 255, 0.4);
}

.connect-button {
  background: linear-gradient(135deg, var(--accent) 0%, var(--accent-dark) 100%);
}

.disconnect-button {
  background: linear-gradient(135deg, var(--error) 0%, #cc4444 100%);
}

/* Estilos existentes para las otras secciones */
.account-info {
  font-size: 1rem;
  color: var(--text-secondary);
  margin-bottom: 20px;
  word-break: break-all;
}

.error-message {
  font-size: 0.9rem;
  color: var(--error);
  margin-bottom: 15px;
}

.section-title {
  font-size: 1.8rem;
  color: var(--accent);
  margin-bottom: 20px;
}

.nft-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
  width: 100%;
  max-width: 1200px;
}

.experience-section {
  margin-top: 30px;
  width: 100%;
  max-width: 1200px;
}

.nft-card {
  background-color: var(--card-bg);
  border-radius: 15px;
  padding: 15px;
  box-shadow: var(--shadow);
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nft-card:hover {
  transform: scale(1.05);
}

.nft-image-container {
  width: 100%;
  max-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  overflow: hidden;
}

.nft-image {
  width: 100%;
  height: auto;
  max-height: 200px;
  object-fit: contain;
  border-radius: 10px;
}

.nft-title {
  font-size: 1.1rem;
  margin-bottom: 8px;
  color: var(--text-primary);
}

.nft-detail {
  font-size: 0.85rem;
  color: var(--text-secondary);
  margin-bottom: 5px;
}

.nft-token-id {
  font-size: 0.8rem;
  color: #999999;
  word-break: break-all;
}

.experience-card {
  background-color: var(--card-bg);
  border-radius: 15px;
  padding: 15px;
  margin-bottom: 15px;
  box-shadow: var(--shadow);
}

.experience-text {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin-bottom: 10px;
}

.event-button {
  background: linear-gradient(135deg, var(--accent) 0%, var(--accent-dark) 100%);
  padding: 10px 20px;
  font-size: 0.9rem;
  max-width: 250px;
}

.event-button:hover {
  transform: scale(1.05);
}

/* Footer */
.footer {
  margin-top: auto;
  padding: 10px 0;
  font-size: 0.8rem;
  color: var(--text-secondary);
}

/* Animaciones */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Media Queries para móviles */
@media (max-width: 768px) {
  .section-title {
    font-size: 1.5rem;
  }

  .nft-grid {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  body {
    padding: 15px;
  }

  .button {
    padding: 10px 20px;
    font-size: 0.9rem;
  }

  .nft-card {
    padding: 10px;
  }

  .nft-image-container {
    max-height: 180px;
  }

  .nft-title {
    font-size: 1rem;
  }

  .nft-detail {
    font-size: 0.8rem;
  }

  .nft-token-id {
    font-size: 0.75rem;
  }

  .experience-card {
    padding: 10px;
  }

  .experience-text {
    font-size: 0.85rem;
  }

  .event-button {
    font-size: 0.85rem;
    padding: 8px 15px;
  }

  .logo {
    width: 120px;
  }

  .tagline {
    font-size: 1rem;
  }

  .info-link {
    font-size: 0.85rem;
  }
}